import {getUrlParameter} from "../../choose_practice";
import store from "../../vuex/store";

// 记录该页面开始的时间
const timestamp = (new Date()).valueOf();

export default {
  name: 'Devpanel',
  data() {
    return {
      ifPanel: true
    }
  },
  computed: {
    // linkTitle: () => i18n.linkTitle[lan],
  },
  mounted() {
    // window.addEventListener('resize', this.resize.bind(this), true)
  },
  methods: {
    skipPractice() {
      function getUrlParameter(name){
        name = name.replace(/[]/,"\[").replace(/[]/,"\[").replace(/[]/,"\\\]");
        var regexS = "[\\?&]"+name+"=([^&#]*)";
        var regex = new RegExp( regexS );
        var results = regex.exec(window.parent.location.href );
        if( results == null ) return ""; else {
          return results[1];
        }
      };

      let ext = getUrlParameter('ext') + '_' + store.state.realGameTime + '_' + store.state.briskNum
      let condition = getUrlParameter('c')
      let seq = getUrlParameter('seq')

      let a = '../../pre/guide-2.html?c=' + condition + '&seq=' + seq + '&ext=' + ext
      if (getUrlParameter('dev') === '1') {
        a = a + '&dev=1'
      }
      setTimeout(function() {window.location.href = a}, 600);
    },


    skipFormalGame() {

      function getUrlParameter(name){
        name = name.replace(/[]/,"\[").replace(/[]/,"\[").replace(/[]/,"\\\]");
        var regexS = "[\\?&]"+name+"=([^&#]*)";
        var regex = new RegExp( regexS );
        var results = regex.exec(window.parent.location.href );
        if( results == null ) return ""; else {
          return results[1];
        }
      };

      // 如果是正式页面，就10分钟后跳转
      const speedCondition = getUrlParameter('c')
      let seq = getUrlParameter('seq')
      let nth_game = getUrlParameter('f')

      let a = ''
      let ext = getUrlParameter('ext') + '_xxx' + store.state.realScore
      a = '../../pre/guide-3.html' + '?c=' + speedCondition + '&seq=' + seq + '&f=' + nth_game + '&ext=' + ext

      if (getUrlParameter('dev') === '1') {
        a = a + '&dev=1'
      }

      window.open(a,"_self")

    },
    addSpeed() {
      this.$store.commit('speedRun', Math.min(12, this.$store.state.speedRun + 1))
    },
    subSpeed() {
      this.$store.commit('speedRun', Math.max(1, this.$store.state.speedRun - 1))
    },
  }
}




